var id = 1;

// Object pattern
class Obj {
	
	constructor(objectCategory, objectName) {
		this.objectCategory = objectCategory;
		this.objectName = objectName;
		this.id = Obj.idGenerator();
	}
	
	static idGenerator() {		
		return id++;
	}	
	
}

// Folder pattern
export class Folder extends Obj {
	
	constructor(folderName, isEmpty = true) {
		super('folder', folderName);
		this.isEmpty = isEmpty;
	}
	
	get properties() {
		let res = {
			id: this.id,
			objectCategory: this.objectCategory,
			objectName: this.objectName,
			isEmpty: this.isEmpty
		};
		return res;
	}
	
}

// File pattern
export class File extends Obj {
	
	constructor(fileName) {
		super('file', fileName, parentFolder);
	}
	
	get properties() {
		let res = {
			id: this.id,
			objectCategory: this.objectCategory,
			objectName: this.objectName
		}
		return res;
	}
	
}

// Parent folder. Save parent folder`s 'id', 'name'
export class parentFolder {
	
	constructor() {
		this.info = {
			id: null,
			objectCategory: 'parentFolder',
			name: null
		};
	}
	
	set information(obj) {
		this.info.id = obj.id;
		this.info.name = obj.name;		
	}
	
	get properties() {
		return this.info;
	}

}


