import React from 'react';
import ReactDOM from 'react-dom';

import './index.css';

//Import outer objects
import {Folder} from './model.js';
import {File} from './model.js';
import {parentFolder} from './model.js';

var parent = new parentFolder();
parent.information = {'id':0, 'name':'Root'};

const layout = {
	list: ['tile', 'list'],
	index: 0
}

// Collection
const collection = {
	list: [],
	content: [],
	view: [],
	insertObj: function(obj) {
		let res = null;
		if (obj.properties.objectCategory !== 'parentFolder') {
			this.list.push(obj);
			res = obj;
		}
		this.content.push(obj);
		this.viewPreparation();
		return res;
	},
	deleteObj: function(idTag) {
		let id = idTag.map((item) => {
			return +item.replace("node","");
		});
		// Delete from list
		id.forEach(function(searchId) {			
			let index = this.list.findIndex((item) => {
				return item.properties.id === searchId;
			});
			this.list.splice(index, 1);		
		}.bind(this));
		// Delete from content
		id.forEach(function(searchId) {			
			let index = this.content.findIndex((item) => {
				return item.properties.id === searchId;
			});
			this.content.splice(index, 1);
		}.bind(this));
		this.viewPreparation();
		// Return deleted 'id'
		return id;
	},
	contentCreate: function(idList) {
		this.content.splice(1, this.content.length-1);
		idList.forEach((id) => {
			let index = this.list.findIndex((obj) => {
				return obj.properties.id === +id;
			});
			if (index !== -1)
				this.content.push(this.list[index]);
		});			
		this.viewPreparation();
	},
	getObjName: function(id) {
		let index = this.list.findIndex((item) => {
			return item.properties.id === id;
		});
		return this.list[index].objectName;		
	},
	search: function(obj) {
		obj = obj.toLowerCase();
		let index = this.list.findIndex((item) => {
			return item.properties.objectName.toLowerCase() === obj;
		});
		let isSearched = (index !== -1) ? this.list[index] : null;
		return isSearched;
	},
	viewPreparation: function() {
		switch (layout.list[layout.index]) {
			case 'tile':
				this.view = this.content.map((item, index) => {
					var obj = item.properties;
					if (obj.objectCategory === 'folder') {
						return (
							<li key={index} id={'node'+obj.id} className="collectionItem">
								<i className="far fa-folder"></i>
								<p className="collectionItem-name">{obj.objectName}</p>
							</li>
						);
					}
					else if (obj.objectCategory === 'file') {
						return (
							<li key={index} id={'node'+obj.id} className="collectionItem">
								<i className="far fa-file"></i>
								<p className="collectionItem-name">{obj.objectName}</p>
							</li>
						);
					}
					else if (obj.objectCategory === 'parentFolder') {
						return (
							<li key={0} id={'node0'} className="collectionItem parentFolder">
								Go to the parent folder
							</li>
						);
					}
					else {
						return (
							<li key={index}>Model error</li>
						);
					}
				});		
				break;
			case 'list':
				this.view = this.content.map((item, index) => {
						var obj = item.properties;
						if (obj.objectCategory === 'folder') {
							return (
								<li key={index} id={'node'+obj.id} className="collectionItem collectionItem-list">
									<i className="far fa-folder"></i>
									<p className="collectionItem-name">{obj.objectName}</p>
								</li>
							);
						}
						else if (obj.objectCategory === 'file') {
							return (
								<li key={index} id={'node'+obj.id} className="collectionItem collectionItem-list file">
									<i className="far fa-file"></i>
									<p className="collectionItem-name">{obj.objectName}</p>
								</li>
							);
						}
						else if (obj.objectCategory === 'parentFolder') {
							return (
								<li key={0} id={'node0'} className="collectionItem collectionItem-list parentFolder">
									Go to the parent folder
								</li>
							);
						}
						else {
							return (
								<li key={index}>Model error</li>
							);
						}
					});		
					break;
				default:
					console.error("Collection view error");
		}		
	}
};

collection.insertObj(parent);

var path = {
	content: [],
	setNode: function(nodeName) {
		this.content.push(nodeName);
		this.view();
	},
	removeNode: function() {
		this.content.pop();
		this.view();
	},
	getFolderName: function() {
		return this.content[this.content.length-1];
	},
	view: function() {
		let msg = this.content.reduce((total, item) => {
			return total + " / " + item;
		});
		msg = "Path: " + msg;
		document.querySelector('.fileManager-path').innerHTML = msg;
	}	
}
path.setNode('Root');

var favourites = {
	list: [],
	view: [],
	insertObj: function(idTag) {		
		let id = idTag.map((item) => {
			return +item.replace("node", "");			
		});
		id.forEach((itemId) => {
			let index = collection.list.findIndex((item) => {
				return item.properties.id === itemId;
			});
			if (index !== -1)
				this.list.push(collection.list[index]);
		});
		this.viewPreparation();
	},
	viewPreparation: function() {
		this.view = this.list.map((item, index) => {
			var obj = item.properties;
			if (obj.objectCategory === 'folder') {
				return (
					<li key={index} className="manager-favourites-list-item">
						<i className="far fa-folder"></i>
						<p>{obj.objectName}</p>
					</li>
				);
			}
			else if (obj.objectCategory === 'file') {
				return (
					<li key={index} className="manager-favourites-list-item">
						<i className="far fa-file"></i>
						<p>{obj.objectName}</p>
					</li>
				);
			}
			else if (obj.objectCategory === 'parentFolder') {
				return (
					<li key={index} className="manager-favourites-list-parentFolder">
						<i className="far fa-folder"></i>					
					</li>
				);
			}			
			else {
				return (
					<li key={index}>Model error</li>
				);
			}
		});
	}
}
favourites.list.push(new parentFolder());
favourites.viewPreparation();

// File manager. Content
class FileManagerContent extends React.Component {
	constructor(props) {
		super(props);
		this.handleClick = this.handleClick.bind(this);
		this.handleDoubleClick = this.handleDoubleClick.bind(this);
	}
	// Select folders or files
	handleClick(e) {
		let item = e.target;
		let liTag = (item.classList.contains('collectionItem')) ? item : item.parentNode;
		
		//console.log(item.classList.contains('collectionItem'));
		
		let idTag = liTag.getAttribute('id');
		var node = document.querySelector('#'+idTag);
		if (node != null && idTag !== 'node0') {
			if (node.classList.contains('selected'))
				node.classList.remove('selected');		
			else
				node.classList.add('selected');
		}
	}
	// Open folder
	handleDoubleClick(e) {
		// - Get opened folder`s 'id'		
		let item = e.target;
		let liTag = (item.classList.contains('collectionItem')) ? item : item.parentNode;
		let idTag = liTag.getAttribute('id');
		if (idTag === 'node0') {
			// Go to the parent folder
			let folderTreeParent = document.getElementById(fileTree.getPosition()).parentNode;
			let folderTreeId = folderTreeParent.getAttribute('id');
			if (folderTreeId !== 'copyNodetree') {			
				// - Get opened folder`s 'id' list
				let folderContentId = fileTree.idList(folderTreeId.replace("copyNode", ""));			
				// - Collection updates
				collection.contentCreate(folderContentId);				
				// - Move through file tree
				fileTree.setPosition(folderTreeId.replace("copyNode", ""));
				// - 'Path' updates
				path.removeNode();					
				// - React 'collectionView' updates
				this.props.managerHandleClick(this.props.view);			
			}
		}
		else if (idTag !== null) {
			// Go to the child folder
			let folderId = +idTag.replace("node", "");
			// - Get opened folder`s 'id' list
			let folderContentId = fileTree.idList(folderId);
			// - Collection updates
			collection.contentCreate(folderContentId);
			// - 'Path' updates
			path.setNode(collection.getObjName(folderId));
			// - Move through file tree
			fileTree.setPosition(folderId);
			// - React 'collectionView' updates
			this.props.managerHandleClick(this.props.view);
		}
	}
	render() {
		return (
			<div className="manager-content">
				<ul className="manager-content-items" onClick={this.handleClick} onDoubleClick={this.handleDoubleClick}>
					{this.props.view}
				</ul>
			</div>
		);
	}
}

// File manager. Tools
class Tools extends React.Component {	
	constructor(props) {
		super(props);
		this.handleClick = this.handleClick.bind(this);
		this.handleFile = this.handleFile.bind(this);
		this.handleCancel = this.handleCancel.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		this.state = {fileReaderClass: false, searchClass: false, search: '', searchClassResult: false, searchResult: {type: '', name: ''}};
	}
	handleCancel() {
		this.setState({fileReaderClass: false});
		this.setState({searchClass: false});
	}
	handleFile() {
		document.querySelector('.progressBar').removeAttribute('style');		
		var reader = new FileReader();
		reader.onload = function() {
			console.log(reader.result);
		}
		reader.onloadend = function() {
			console.log(reader.result);
		}
		document.querySelector('.progressBar').classList.add('long');
		setTimeout(function() {
			document.querySelector('.progressBar').style.transition = "all 0s";
			document.querySelector('.progressBar').classList.remove('long');
			var fileName = document.querySelector('#inputFile').files[0].name;
			let file = new File(fileName);
			let insertedObj = collection.insertObj(file);
			this.setState({fileReaderClass: false});
			this.props.onTollsButtonClicked();
			fileTree.insertObj(insertedObj);
		}.bind(this), 3000);
	}
	handleChange(e) {
		this.setState({search: e.target.value});
		this.setState({searchClassResult: false});
	}
	handleSubmit() {
		let obj = this.state.search;
		let isSearched = collection.search(obj);
		console.log(isSearched);
		var res = {};
		if (isSearched !== null) {
			res.type = isSearched.objectCategory;
			res.name = isSearched.objectName;
		}
		else {
			res.type = 'Not found';
			res.name = 'Not found';
		}
		this.setState({searchClassResult: true, searchResult: res});
	}
	handleClick(e) {
		switch (e) {
			case 1:
				// Create folder
				var folderName = prompt("Folder name. Must be shorter then 8 symbols", "none");
				if (folderName.length>0 && folderName.length <= 8) {
					let folder = new Folder(folderName);
					let insertedObj = collection.insertObj(folder);		
					this.props.onTollsButtonClicked();				
					// - Insert this object to the file tree
					fileTree.insertObj(insertedObj);
				}
				break;
			case 2:
				// Delete file or folder
				// - Get selected
				let tag = document.querySelectorAll('#manager .selected');
				if (tag.length > 0) {
					let idTag = [];
					tag.forEach((item) => {
						idTag.push(item.getAttribute('id'));						
					});
					// - Delete from the collection
					let deletedObjId = collection.deleteObj(idTag);
					// - Remove '.selected'
					document.querySelectorAll('.selected').forEach((item) => {
						item.classList.remove('selected');
					});
					// - Remove from the file tree
					fileTree.removeObj(deletedObjId);
					// - React 'collectionView' updates
					this.props.onTollsButtonClicked();
				}
				else
					alert("Please, select at least one item");
				break;
			case 3:
				// Add file or folder to 'favourites'
				tag = document.querySelectorAll('#manager .selected');
				if (tag.length > 0) {
					let idTag = [];
					tag.forEach((item) => {
						idTag.push(item.getAttribute('id'));						
					});
					// - Copy to the 'Favourites'
					favourites.insertObj(idTag);
					// - Remove '.selected'
					document.querySelectorAll('.selected').forEach((item) => {
						item.classList.remove('selected');
					});					
					// - React 'collectionView' updates
					this.props.onTollsButtonClicked();
				}
				else
					alert("Please, select at least one item");				
				break;
			case 4:
				// File uploads
				this.setState({fileReaderClass: true});				
				break;
			case 5:
				// Search
				this.setState({searchClass: true});
				break;
			default:
				console.error("Tools error");
		}
	}	
	render() {
		var fileReaderClass = "manager-tools-upload";
		var searchClass = "manager-tools-search";
		var searchClassResult = "manager-tools-search-result";
		const searchResult = this.state.searchResult;
		if (this.state.fileReaderClass) {
			fileReaderClass += ' implement';
		}
		if (this.state.searchClass) {
			searchClass += ' implement';
		}
		if (this.state.searchClassResult) {
			searchClassResult += ' visible';
		}
		return (
			<div className="manager-tools">
				<button type="button" onClick={(e) => this.handleClick(1)}>Create folder</button>
				<button type="button" onClick={(e) => this.handleClick(2)}>Delete</button>
				<button type="button" onClick={(e) => this.handleClick(3)}>Add to favourites</button>
				<button type="button" onClick={(e) => this.handleClick(4)}>Upload</button>
				<button type="button" onClick={(e) => this.handleClick(5)}>Search</button>
				<div className={fileReaderClass}>
					<input id="inputFile" type="file" onChange={this.handleFile}/>
					<button type="button" className="cancel" onClick={this.handleCancel}>Cancel</button>
				</div>
				<div className={searchClass}>
					<input type="text" onChange={this.handleChange}/>
					<button type="button" className="submit" onClick={this.handleSubmit}>Submit</button>
					<button type="button" className="cancel" onClick={this.handleCancel}>Cancel</button>
					<div className={searchClassResult}>
						<span>Type:{searchResult.type}</span>
						<span>Name:{searchResult.name}</span>
					</div>
				</div>
			</div>
		);
	}	
}

// File manager
class Manager extends React.Component {
	
	constructor(props) {
		super(props);		
		this.objClick = this.objClick.bind(this);
		this.layoutHandler = this.layoutHandler.bind(this);
		this.state = {collectionView: collection.view, folderName: path.getFolderName(), favouritesView: favourites.view};
	}
	
	// Content view updates
	objClick() {
		this.setState({collectionView: collection.view, folderName: path.getFolderName(), favouritesView: favourites.view});		
	}
	
	// Switch layout
	layoutHandler(e) {
		layout.index = e;
		collection.viewPreparation();
		this.setState({collectionView: collection.view});
		console.log(collection.view);
	}
	
	render() {
		const collectionView = this.state.collectionView;
		const folderName = this.state.folderName;
		const favouritesView = this.state.favouritesView;
		return (
			<div>
				<div className="manager-options">
					<span className="manager-options-folder">Folder name: {folderName}</span>
					<div className="manager-options-layout">
						<i className="fas fa-bars tile icon" title="Tile layout" onClick={(e) => this.layoutHandler(0)}></i>
						<i className="fas fa-list list icon" title="List layout" onClick={(e) => this.layoutHandler(1)}></i>						
					</div>
				</div>
				<Tools view={collectionView} onTollsButtonClicked={this.objClick}/>
				<div className="progressBar"></div>
				<FileManagerContent view={collectionView} managerHandleClick={this.objClick} />
				<div className="manager-favourites">
					<p>Favourites</p>
					<ul className="manager-favourites-list">
						{favouritesView}						
					</ul>
				</div>
			</div>
		);
	}
	
}

// File tree
class FileTree {
	
	constructor(rootId) {
		this.location = rootId;
	}
	
	// - Insert new node (file or folder)
	insertObj(obj) {		
		let parentTag = document.getElementById(this.location);		
		var node = document.createElement('li');
		node.setAttribute('id', 'copyNode' + obj.id);
		node.classList.add('tree-item');
		parentTag.appendChild(node);
		var nodeIcon = document.createElement('i');
		let nodeIconClass = (obj.objectCategory === 'folder') ? 'fa-folder' : 'fa-file';
		nodeIcon.classList.add('far');
		nodeIcon.classList.add(nodeIconClass);
		node.appendChild(nodeIcon);
		var nodeName = document.createElement('span');
		nodeName.innerHTML = obj.objectName;
		node.appendChild(nodeName);
	}
	
	// - Remove node(s) that was(were) deleted from the collection
	removeObj(id) {
		id.forEach((item) => {					
			let nodeToRemove = document.getElementById('copyNode' + item);
			document.getElementById(this.location).removeChild(nodeToRemove);			
		});
	}

	//	- Get folder`s content id
	idList(folderId) {
		let idList = [];
		let tagList = document.querySelectorAll('#copyNode' + folderId + '>li');
		tagList.forEach((item) => {
			idList.push(item.getAttribute('id').replace("copyNode", ""));
		});		
		return idList;
	}
	
	setPosition(id) {
		document.querySelector('#' + this.location).classList.remove('tree-location');
		this.location = 'copyNode' + id;
		document.querySelector('#' + this.location).classList.add('tree-location');
	}
	
	getPosition() {
		return this.location;
	}
	
}

ReactDOM.render(
	<Manager />,
	document.querySelector('#manager')
);

var fileTree = new FileTree('copyNodetree');
fileTree.insertObj({id: 'root', objectCategory:'folder', objectName: 'Root'});
fileTree.setPosition('root');


